import React from 'react';
import { useParams } from 'react-router-dom';
import CardsData from "../data.js/CardsData";
import { Grid, Button } from '@mui/material';
import Footer from '../components/Footer';

const SingleProducts = ({addToCart}) => {

  const { id } = useParams();

  return (
    <>
    <h1 style={{textAlign:"center", paddingTop:"100px", margin:"0", fontSize:"40px", color:"#591606"}}>Dettagli Prodotto</h1>
    <div className='singlep'>
      {CardsData.filter(prodotto => prodotto.id === id).map((p) => {
        return (
          <Grid container spacing={2} style={{alignItems:"center", padding:"60px 0"}}>
            <Grid item md="6" sm="12" xs="12">
              <div style={{textAlign:"center"}}>
                <img src={p.img} alt={p.title} style={{width:"85%"}} />
              </div>
            </Grid>
            <Grid item md="6" sm="12" xs="12">
              <div>
                <h3 style={{fontSize:"24px", fontFamily:"inter", fontWeight:"600"}}>{p.title}</h3>
                <span style={{fontFamily:"inter", color:"#666666", fontSize:"12px"}}>{p.stars}</span>
                <p style={{color:"#666666", fontFamily:"inter"}}>{p.description}</p>
                <span style={{fontFamily:"inter", color:"#666666"}}>Disponibilit: {p.available}</span>
                <h2 style={{fontFamily:"inter", color:"#b33012"}}>{p.price}.00</h2>
                <Button onClick={()=> addToCart(p)} style={{fontFamily:"inter", backgroundColor:"#b33012"}} variant='contained'>Add to cart</Button>
              </div>
                <span style={{color:"#666666", display:"block", paddingTop:"40px"}}>Categoria: {p.category}</span>
            </Grid>
          </Grid>
        )
      })}
    </div>
    <Footer />
    </>
  )
}

export default SingleProducts