import { Button, Grid, Input, Tab, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material'
import React from 'react'
import Footer from '../components/Footer'
import QuantityIcon from '@mui/icons-material/ProductionQuantityLimits';
import DeleteIcon from '@mui/icons-material/DeleteForever';

const Carrello = ({ cart, setCart }) => {

  const removeProduct = (productId) => {
    const updateCart = cart.filter(product => product.id !== productId);
    setCart(updateCart);
  }

  return (
    <> <h1 style={{ textAlign: "center", paddingTop: "100px", margin: "0", fontSize: "40px", color: "#591606" }}>Carrello</h1>

      {cart.length === 0 ? (
        <div style={{ textAlign: "center", padding: "180px" }}>
          <h3>Il tuo carrello  vuoto!</h3>
          <QuantityIcon />
        </div>
      ) : (
        <div className='carrello'>
          <TableContainer>
            <Table className='table'>
              <TableHead>
                <TableRow style={{backgroundColor:"#ffa00b"}}>
                  <TableCell></TableCell>
                  <TableCell>Prodotto</TableCell>
                  <TableCell>Prezzo</TableCell>
                  <TableCell>Quantit</TableCell>
                  <TableCell></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>

                {cart.map((product) => {
                  return (
                    <TableRow key={product.id} style={{backgroundColor:"#b33012"}}>
                      <TableCell><img src={product.img} alt={product.title} width={75} /></TableCell>
                      <TableCell sx={{color:"#d5d5d5"}}>{product.title}</TableCell>
                      <TableCell sx={{color:"#d5d5d5"}}>{product.price}.00</TableCell>
                      <TableCell sx={{color:"#d5d5d5"}}>1</TableCell>
                      <TableCell><span onClick={() => removeProduct(product.id)} style={{cursor:"pointer", fontWeight:"800", color:"#fff", marginLeft:"12px"}}>X</span></TableCell>
                    </TableRow>
                  )
                })}

              </TableBody>
            </Table>
          </TableContainer>

        </div >
      )}

      <Footer />
    </>
  )
}

export default Carrello
