import { List, ListItemButton, ListItemText, SwipeableDrawer } from '@mui/material'
import CloseIcon from '@mui/icons-material/Close';
import React from 'react'
import { Link } from 'react-router-dom'

const Drawer = ({ attivaDrawer, drawerOpen }) => {

    return (
        <SwipeableDrawer PaperProps={{style:{backgroundColor:"#ffa00b", fontWeight:"600"}}} open={drawerOpen} onClose={() => attivaDrawer(false)} onOpen={() => attivaDrawer(true)}>
            <CloseIcon onClick={() => attivaDrawer(false)} style={{ cursor: "pointer", padding:"14px"}} />
            <List>
                <ListItemButton component={Link} to="/Burger-App">
                    <ListItemText primary={<span style={{ fontWeight: 700 }}>Home</span>} />
                </ListItemButton>
                <ListItemButton component={Link} to="/Chisiamo">
                    <ListItemText primary={<span style={{ fontWeight: 700 }}>Info</span>} />
                </ListItemButton>
                <ListItemButton button component={Link} to="/Prodotti">
                    <ListItemText primary={<span style={{ fontWeight: 700 }}>Prodotti</span>} />
                </ListItemButton>
            </List>
        </SwipeableDrawer>
    )
}

export default Drawer
