import { Avatar, Box, Card, CardContent, Typography } from "@mui/material";
import React, { Component } from "react";
import Slider from "react-slick";
import dataReviews from "../data.js/ReviewsData"

export default class Responsive extends Component {
    render() {
        var settings = {
            dots: true,
            infinite: true,
            speed: 1000,
            slidesToShow: 3,
            slidesToScroll: 1,
            initialSlide: 0,
            autoplay: false,
            autoplaySpeed: 3000,
            responsive: [
                {
                    breakpoint: 1125,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 3,
                        infinite: true,
                        dots: true
                    }
                },
                {
                    breakpoint: 760,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1,
                        initialSlide: 2
                    }
                }
            ]
        };
        return (
            <div style={{textAlign:"center", paddingBottom:"120px"}}>
                <h2 style={{fontSize:"30px", color:"#591606", padding:"50px 0", fontFamily:"londrina solid"}}> Recensioni </h2>
                <Slider {...settings}>
                    {dataReviews.map((el) => {
                        return (
                            <Card sx={{ maxWidth: 350, borderRadius:"10px", backgroundColor: "beige"}}>
                                <CardContent>
                                    <Typography variant="body2" style={{textAlign:"initial"}}>
                                        {el.description}
                                    </Typography>
                                <Typography sx={{display:"flex", alignItems:"center", marginTop:"20px", fontWeight:"600"}}>
                                    <Avatar sx={{marginRight:"10px"}} alt={el.name} src={el.img} />
                                    {el.name}
                                </Typography>
                                </CardContent>
                            </Card>
                        )
                    })}
                </Slider>
            </div>
        );
    }
}