import React from 'react'
import img1 from "../images/category-1.png"
import img2 from "../images/category-2.png"
import img3 from "../images/category-3.png"
import img4 from "../images/category-4.png"
import img5 from "../images/category-5.png"
import img6 from "../images/category-6.png"
import { Grid } from '@mui/material'

const Snackys = () => {
  return (
      <div className='sec2' style={{ textAlign: "center" }}>
        <h2 style={{ fontSize: "30px", fontFamily: "londrina solid", color:"#591606" }}>Snacky's Menu</h2>
        <Grid container spacing={2} style={{ justifyContent: "center" }}>
          <Grid item xl="2" md="2">
            <img src={img1} alt="sezione 1" style={{width:"75%"}} />
            <h3>Hamburger</h3>
          </Grid>
          <Grid item xl="2" md="2">
            <img src={img2} alt="sezione 2" style={{width:"75%"}} />
            <h3>Nuggets</h3>
          </Grid>
          <Grid item xl="2" md="2">
            <img src={img3} alt="sezione 3" style={{width:"75%"}} />
            <h3>Novit</h3>
          </Grid>
          <Grid item xl="2" md="2">
            <img src={img4} alt="sezione 4" style={{width:"75%"}} />
            <h3>Piadina</h3>
          </Grid>
          <Grid item xl="2" md="2">
            <img src={img5} alt="sezione 5" style={{width:"75%"}} />
            <h3>Breakfast</h3>
          </Grid>
          <Grid item xl="2" md="2">
            <img src={img6} alt="sezione 6" style={{width:"75%"}} />
            <h3>Drinks</h3>
          </Grid>
        </Grid>
      </div>
  )
}

export default Snackys;