import { Facebook, Instagram, Pinterest, Twitter } from '@mui/icons-material'
import { Grid } from '@mui/material'
import React from 'react'

const Footer = () => {
    return (
        <div className='footer'>
            <Grid container spacing={2}>
                <Grid item xl="4" md="4" sm="4" xs="12">
                    <h3 style={{ color: "#b33012" }}>Contattaci</h3>
                    <span style={{ color: "#d5d5d5" }}>Per ordini o info</span>
                    <h5 style={{ color: "#d5d5d5" }}>+39 333-0000000</h5>
                    <p style={{ color: "#fff" }}>Ci trovi in Via Lorem Ipsum 23, Los Angeles</p>
                </Grid>
                <Grid item xl="4" md="4" sm="4" xs="12">
                    <h3 style={{ color: "#b33012" }}>Orari Apertura</h3>
                    <div style={{ display: "flex", flexDirection: "column", color: "#fff" }}>
                        <span>Marted 9:00 - 20:30</span>
                        <span>Mercoled 9:00 - 20:30</span>
                        <span>Gioved 9:00 - 20:30</span>
                        <span>Venerd 9:00 - 20:30</span>
                        <span>Sabato 9:00 - 01:30</span>
                        <span>Domenica 9:00 - 00:00</span>
                    </div>
                </Grid>
                <Grid item xl="4" md="4" sm="4" xs="12">
                    <h3 style={{ color: "#b33012" }}>Seguici sui Social!</h3>
                    <Instagram style={{ color: "#d5d5d5", cursor: "pointer" }} />
                    <Facebook style={{ color: "#d5d5d5", cursor: "pointer" }} />
                    <Pinterest style={{ color: "#d5d5d5", cursor: "pointer" }} />
                    <Twitter style={{ color: "#d5d5d5", cursor: "pointer" }} />
                </Grid>
            </Grid>

            <div style={{textAlign:"center", color:"#d5d5d5", paddingTop:"50px"}}>
                <p> 2023 Burger App by Potenza Michele All Rights Reserved</p>
            </div>
        </div>
    )
}

export default Footer