import { BrowserRouter as Router, Routes, Route } from "react-router-dom"
import Home from "./pages/Home"
import Chisiamo from "./pages/Chisiamo"
import Prodotti from "./pages/Prodotti"
import Navbar from "./components/Navbar";
import SingoliProdotti from "./pages/SingoliProdotti";
import Carrello from "./pages/Carrello";
import { useState } from "react";
import { ThemeProvider, createTheme } from "@mui/material/styles";

function App() {

  const [ cart, setCart ] = useState([]);

  const addToCart = (product) => {
    setCart([...cart, product])
  }

  const themeCustom = createTheme({
    typography: {
      fontFamily: 'Inter, sans-serif',
    }
  })

  
  return (
    <ThemeProvider theme={themeCustom}>
      <Router>
        <Navbar cart={cart} />
        <Routes>
          <Route path="/Burger-App" element={<Home cart={cart} addToCart={addToCart} />} />
          <Route path="/Carrello" element={<Carrello cart={cart} setCart={setCart}/>} />
          <Route path="/Chisiamo" element={<Chisiamo />} />
          <Route path="/Prodotti" element={<Prodotti addToCart={addToCart} cart={cart} />} />
          <Route path="/Prodotti/:id" element={<SingoliProdotti addToCart={addToCart} />} /> 
        </Routes>
      </Router>
    </ThemeProvider>
  );
}

export default App;
