import React from 'react'
import CarouselShop from "./CarouselShop"
import CarouselRewies from "./CarouselRewies"
import Snackys from './Snackys'
import Footer from './Footer'
import Newsletter from './Newsletter'
import Sec1 from './Sec1'

const Main = ({cart, addToCart}) => {
  return (
    <>
      <Sec1 />
      <Snackys />
      {/* <Cards /> */}
      <CarouselShop cart={cart} addToCart={addToCart} />
      <CarouselRewies />
      <Newsletter />
      <Footer />

    </>
  )
}

export default Main