import { Button, Card, CardActionArea, CardContent, Grid, Typography } from '@mui/material';
import React from 'react'
import data from '../data.js/CardsData';
import { Link } from 'react-router-dom';
import ShoppingCart from '@mui/icons-material/ShoppingCartCheckout';
import Footer from '../components/Footer';

const Carrello = ({addToCart, cart}) => {

  return (
    <>
      <h1 style={{ textAlign: "center", paddingTop: "100px", margin: "0", fontSize: "40px", color: "#591606" }}>Prodotti</h1>
      <div style={{ maxWidth: "1000px", margin: "0 auto", padding: "60px 20px" }}>
        <Grid container spacing={2} style={{justifyContent:"center"}}>
          {data.map((element) => {
            return (
              <Grid item md="3" sm="4">
                <Card key={element.id} sx={{ maxWidth: 200, borderRadius: "10px" }}>
                  <CardActionArea sx={{ textAlign: "center", backgroundColor: "#b33012" }}>
                    <img src={element.img} alt={element.title} style={{ width: "70%", display: "inline" }} />
                    <CardContent>
                      <Typography variant='h5' style={{ fontFamily: "inter", fontWeight: "600", color: "#d5d5d5", paddingBottom: "6px", fontSize: "18px" }}>
                        {element.title}
                      </Typography>
                      <Typography variant="body2" style={{ fontFamily: "inter", color: "#d5d5d5", paddingBottom: "6px", fontSize: "12px" }}>
                        {element.description}
                      </Typography>
                      <Typography style={{ fontFamily: "inter", color: "#d5d5d5" }}>
                        {element.price}.00
                      </Typography>
                      <Link to={`/Prodotti/${element.id}`}>
                        <Button style={{ backgroundColor: "#ffa00b", marginTop: "10px", fontSize: "10px" }} variant='contained'>Pi info</Button>
                      </Link>
                      <Button style={{ backgroundColor: cart.includes(element) ? "#00aa00" : "#ffa00b", marginTop: "10px", fontSize: "10px" }} variant="contained" onClick={()=> addToCart(element)}><ShoppingCart sx={{width:"14px"}} />{cart.includes(element) ? "Aggiunto" : "Aggiungi"}</Button>
                    </CardContent>
                  </CardActionArea>
                </Card>
              </Grid>
            )
          })}
        </Grid>
      </div>

      <Footer />
    </>

  )
}

export default Carrello;
