import { AppBar, Badge, Tab, Tabs, Toolbar, Typography } from '@mui/material'
import MenuIcon from '@mui/icons-material/Menu';
import React, { useState } from 'react'
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { Link } from 'react-router-dom';
import Drawer from './Drawer';


const Navbar = ({ cart }) => {

    const [drawerOpen, setDrawerOpen] = useState(false);

    const attivaDrawer = (open) => {
        setDrawerOpen(open);
    }

    return (
        <AppBar position='fixed' style={{ padding: "0", backgroundColor: "#000" }}>
            <Toolbar style={{ justifyContent: "space-between" }}>

                <div>
                    <Typography variant='h6' sx={{ display: { md: "block", xs: "none" }, fontFamily: "Londrina Solid" }}>
                        BURGER APP
                    </Typography>
                    <Typography sx={{ marginTop: "4px", display: { md: "none", xs: "block" } }} onClick={() => attivaDrawer(true)}>
                        <MenuIcon style={{ cursor: "pointer" }} />
                    </Typography>
                </div>

                <div>
                    <Tabs>
                        <Tab sx={{ color: "#fff", "&:hover": { color: "#ffa00b" } }} style={{ fontWeight: "600" }} label="Home" component={Link} to="/Burger-App" />
                        <Tab sx={{ color: "#fff", "&:hover": { color: "#ffa00b" } }} style={{ fontWeight: "600" }} label="Info" component={Link} to="/Chisiamo" />
                        <Tab sx={{ color: "#fff", "&:hover": { color: "#ffa00b" } }} style={{ fontWeight: "600" }} label="Prodotti" component={Link} to="/Prodotti" />
                    </Tabs>
                </div>

                <div>
                    <Typography style={{ marginTop: "4px" }}>
                        <Link to={"/Carrello"}>
                            <Badge badgeContent={cart.length > 0 ? cart.length : 0}>
                                <ShoppingCartIcon style={{ cursor: "pointer", color: "#fff" }} />
                            </Badge>
                        </Link>
                    </Typography>
                </div>

            </Toolbar>
            <Drawer attivaDrawer={attivaDrawer} drawerOpen={drawerOpen} />
        </AppBar>
    )
}

export default Navbar