import { Button, Grid } from '@mui/material'
import React from 'react'
import hamb from "../images/Hamburger.png"
import { Link } from 'react-router-dom'


const Sec1 = () => {
  return (
    <div className='sec1-backg'>
        <div className='sec1'>
          <Grid container spacing={2} sx={{ alignItems: "center" }}>
            <Grid item xl="6" md="6" xs="12">
              <h1 className='Burger-title'>Grilled Beef <br />Burger</h1>
              <span style={{ color: "#d5d5d5"}}>Special Deal on only TUESDAY every weeks</span>
              <h3 style={{ color: "#d5d5d5", fontWeight:"500"}}>Buy 2 Get 3th FREE</h3>
              <Link to={"/Prodotti/7"}>
              <Button variant="contained" style={{ backgroundColor: "#ffa00b"}}>Ordina ora</Button>
              </Link>
            </Grid>
            <Grid item xl="6" md="6" xs="12">
              <div>
                <img src={hamb} alt='Hamburger' style={{ width: "75%" }} />
              </div>
            </Grid>
          </Grid>
        </div>
      </div>
  )
}

export default Sec1