import React, { Component } from 'react'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from 'react-slick';
import data from '../data.js/CardsData';
import { Button, Card, CardActionArea, CardContent, Typography } from '@mui/material';
import { Link } from 'react-router-dom';
import ShoppingCart from '@mui/icons-material/ShoppingCartCheckout';

export default class Responsive extends Component {
    render() {

        const {cart, addToCart} = this.props;

        var settings = {
            dots: true,
            infinite: true,
            speed: 1000,
            slidesToShow: 4,
            slidesToScroll: 1,
            initialSlide: 0,
            autoplay: false,
            autoplaySpeed: 3000,
            responsive: [
                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 1,
                        infinite: true,
                        dots: true
                    }
                },
                {
                    breakpoint: 800,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 1,
                        initialSlide: 2
                    }
                },
                {
                    breakpoint: 600,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 1,
                        initialSlide: 2
                    }
                },
                {
                    breakpoint: 550,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }
            ]
        };
        return (
            <div style={{textAlign:"center", padding:"150px 0"}}>
                <h2 style={{fontSize:"30px", color:"#591606", padding:"50px 0"}}> Il nostro Best Seller </h2>
                <Slider {...settings}>
                    {data.map((element) => {
                        return (
                            <Card sx={{ maxWidth: 200, borderRadius: "10px" }}>
                            <CardActionArea sx={{ textAlign: "center", backgroundColor: "#b33012" }}>
                              <img src={element.img} alt={element.title} style={{ width: "70%", display: "inline" }} />
                              <CardContent>
                                <Typography variant='h5' style={{fontWeight: "600", color: "#d5d5d5", paddingBottom: "6px", fontSize: "18px" }}>
                                  {element.title}
                                </Typography>
                                <Typography variant="body2" style={{color: "#d5d5d5", paddingBottom: "6px", fontSize: "12px" }}>
                                  {element.description}
                                </Typography>
                                <Typography style={{color: "#d5d5d5" }}>
                                  {element.price}.00
                                </Typography>
                                <Link to={`/Prodotti/${element.id}`}>
                                  <Button style={{ backgroundColor: "#ffa00b", marginTop: "10px", fontSize: "10px" }} variant='contained'>Pi info</Button>
                                </Link>
                                <Button style={{ backgroundColor: cart.includes(element) ? "#00aa00" : "#ffa00b", marginTop: "10px", fontSize: "10px" }} variant="contained" onClick={()=> addToCart(element)}><ShoppingCart sx={{width:"14px"}} />{cart.includes(element) ? "Aggiunto" : "Aggiungi"}</Button>
                              </CardContent>
                            </CardActionArea>
                          </Card>
                        )
                    })}
                </Slider>
            </div>
        );
    }
}